import React from 'react';
import { useTheme, Box } from '@mui/material';

import AboutUs from './AboutUs';
import AboutNFT from './AboutNFT';
import Projects from './Projects';
import Roadmap from './Roadmap';
import Partners from './Partners';

const Home = (): JSX.Element => {
  const theme = useTheme();

  return (
    <Box>
      <Box id="about">
        <AboutUs />
      </Box>
      <Box id="nft">
        <AboutNFT />
      </Box>
      <Box id="projects" bgcolor={ theme.palette.mode === 'dark' ? 'grey.900' : 'grey.100' }>
        <Projects />
      </Box>
      <Box id="roadmap">
        <Roadmap />
      </Box>
      <Box id="partners" bgcolor={ theme.palette.mode === 'dark' ? 'grey.900' : 'grey.100' }>
        <Partners />
      </Box>
    </Box>
  );
};

export default Home;
