import React, { useEffect } from 'react';
import { Container, Box, Typography } from '@mui/material';

import mainBackground from '../../assets/images/main-background.jpg';

const AboutUs = (): JSX.Element => {
  useEffect(() => {
    const jarallaxInit = async () => {
      const jarallaxElems = document.querySelectorAll('.jarallax');
      if (!jarallaxElems || (jarallaxElems && jarallaxElems.length === 0)) {
        return;
      }

      const { jarallax } = await require('jarallax');
      jarallax(jarallaxElems, { speed: 0.2 });
    };

    jarallaxInit();
  });

  return (
    <Box
      className={'jarallax'}
      sx={{
        height: '100vh',
        marginTop: -8,
        paddingY: 52,
        position: 'relative',
        display: 'flex',
        alignItems: 'center'
      }}
    >
      <Box
        className={'jarallax-img'}
        sx={{
          position: 'absolute',
          objectFit: 'cover',
          fontFamily: 'object-fit: cover;',
          top: 0,
          left: 0,
          width: '100%',
          height: '100%',
          zIndex: -1,
          backgroundRepeat: 'no-repeat',
          backgroundSize: 'cover',
          backgroundPosition: 'center center',
          backgroundImage: 'url(' + mainBackground + ')',
        }}
      />
      <Container>
        <Box>
          <Typography
            variant="h2"
            gutterBottom
            sx={{
              fontWeight: 700,
              color: 'common.white',
              textTransform: 'uppercase',
            }}
          >
            {'About Us'}
          </Typography>
          <Typography
            variant="h6"
            component="p"
            sx={{
              marginBottom: 3,
              color: 'common.white',
            }}
          >
            {'FireDAO is an effective and safe way to work with like-minded folks around the world, built on the Polygon blockchain.'}
          </Typography>
          <Typography
            variant="h6"
            component="p"
            sx={{
              marginBottom: 3,
              color: 'common.white',
            }}
          >
            {'It\'s like an internet-native business that\'s collectively owned and managed by its members. There are built-in treasuries that no one has the authority to access without the approval of the group. Decisions are governed by proposals and voting to ensure everyone in the organization has a voice.'}
          </Typography>
          <Typography
            variant="h6"
            component="p"
            sx={{
              color: 'common.white',
            }}
          >
            {'There\'s no CEO who can authorize spending based on their own whims and no chance of a dodgy CFO manipulating the books. Everything is out in the open and the rules around spending are baked into the DAO via its code.'}
          </Typography>
        </Box>
      </Container>
    </Box>
  );
};

export default AboutUs;
